export default () => {
    setTimeout(() => {
        let isDragging = false;
        let dragElement = document.getElementById('bottomSheet');
        let initialY = 0;
        let currentY = 0;
        let targetY = 523; // Î™©Ìëú ÏúÑÏπò (Ïòà: 200px)
        let isReturning = false; // Ï∂îÍ∞ÄÎêú Î≥ÄÏàò: ÏõêÎûò ÏúÑÏπòÎ°ú ÎèåÏïÑÍ∞ÄÎäî Ï§ëÏù∏ÏßÄ Ïó¨Î∂Ä

        dragElement.addEventListener('mousedown', startDrag);
        dragElement.addEventListener('mousemove', drag);
        dragElement.addEventListener('mouseup', stopDrag);
        dragElement.addEventListener('click', (event) => {
            if (event.target.classList.contains('navcontent')) {
                event.target.style.backgroundColor = 'red';
                event.target.style.color = 'white';

                const routePath = decodeURI(location.hash);
                const routeSplit = routePath.split('/');

                if (routeSplit.length >= 5) {
                    routeSplit.pop();
                    routeSplit.push(event.target.innerText);
                    const path = routeSplit.join('/')
                    location.hash = path;
                } else {
                    location.hash += '/' + event.target.innerText
                }
            }
            const clickedDiv = event.target.closest('.navcontent');

            if (clickedDiv) {
                if (prevClickedDiv) {
                    // Ïù¥Ï†ÑÏóê ÌÅ¥Î¶≠Ìïú divÎ•º ÏõêÎûò ÏÉÅÌÉúÎ°ú ÎêòÎèåÎ¶ΩÎãàÎã§.
                    prevClickedDiv.style.backgroundColor = ''; // Í∏∞Î≥∏ Î∞∞Í≤ΩÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω
                    prevClickedDiv.style.color = ''; // Í∏∞Î≥∏ ÌÖçÏä§Ìä∏ ÏÉâÏÉÅÏúºÎ°ú Î≥ÄÍ≤Ω
                }

                clickedDiv.style.backgroundColor = 'red'; // ÌÅ¥Î¶≠Ìïú divÏùò Ïä§ÌÉÄÏùºÏùÑ Î≥ÄÍ≤ΩÌï©ÎãàÎã§.
                clickedDiv.style.color = 'white';

                // ÌòÑÏû¨ ÌÅ¥Î¶≠Ìïú divÎ•º prevClickedDiv Î≥ÄÏàòÏóê Ï†ÄÏû•Ìï©ÎãàÎã§.
                prevClickedDiv = clickedDiv;
            }
        })

        function startDrag(event) {
            isDragging = true;
            if (!isReturning) { // ÏõêÎûò ÏúÑÏπòÎ°ú ÎèåÏïÑÍ∞ÄÎäî Ï§ëÏù¥ ÏïÑÎãê ÎïåÎßå Ï¥àÍ∏∞Ìôî
                initialY = event.clientY;
                currentY = event.clientY - initialY;
            }
            console.log(event.clientY);
        }

        function drag(event) {
            if (isDragging) {
                if (!isReturning) { // ÏõêÎûò ÏúÑÏπòÎ°ú ÎèåÏïÑÍ∞ÄÎäî Ï§ëÏù¥ ÏïÑÎãê ÎïåÎßå ÎìúÎûòÍ∑∏ Ï†ÅÏö©
                    currentY = event.clientY - initialY;
                    if (currentY < targetY) {
                        dragElement.style.transform = `translateY(${currentY}px)`;
                    } else {
                        dragElement.style.transform = `translateY(${targetY}px)`;
                        stopDrag();
                    }
                }
            }
        }

        function stopDrag() {
            isDragging = false;
            if (currentY >= targetY) {
                isReturning = true;
                dragElement.style.transform = 'translateY(0)';
            } else {
                initialY = currentY;
            }
            console.log(initialY, 'stop drag initial Y');
        }
        let prevClickedDiv = null;
    }, 1000);
}